// images references in the manifest
import "../../assets/icon-16.png";
import "../../assets/icon-32.png";
import "../../assets/icon-80.png";

/* global console, document, Excel, Office */
const limit = 1000;

Office.onReady(info => {
  if (info.host === Office.HostType.Excel) {
    document.getElementById("sideload-msg").style.display = "none";
    document.getElementById("app-body").style.display = "flex";
    document.getElementById("setupKgMl").onclick = setupKgMl;
    document.getElementById("setup").onclick = setup;
  }
});

export async function setupKgMl() {
  try {
    await Excel.run(async context => {
      const sheet = initSheet(context, 'Resultados (kg o ml)');
      const range = initReferenceSheet(context);

      await context.sync().then(function () {
        const list = loadData(range, 5);
        const fmt = '_(#,##0.00_);_((#,##0.00);_(" - "??_);_(@_)';
        drawResults(sheet, list, fmt);
        sheet.activate();
      });
    });
  } catch (error) {
    console.error(error);
  }
}

export async function setup() {
  try {
    await Excel.run(async context => {
      const sheet = initSheet(context, 'Resultados ($)');
      const range = initReferenceSheet(context);

      await context.sync().then(function () {
        const list = loadData(range, 6);
        const fmt = '_($* #,##0.00_);_($* (#,##0.00);_($* " - "??_);_(@_)';
        drawResults(sheet, list, fmt);
        sheet.activate();
      });
    });
  } catch (error) {
    console.error(error);
  }
}

function loadData(range: Excel.Range, idx: number): Item[] {
  const list: Item[] = [];
  loadDataForDay(range, list, "monday", 0, idx);
  loadDataForDay(range, list, "tuesday", 9, idx + 9);
  loadDataForDay(range, list, "wednesday", 18, idx + 18);
  loadDataForDay(range, list, "thursday", 27, idx + 27);
  loadDataForDay(range, list, "friday", 36, idx + 36);
  loadDataForDay(range, list, "saturday", 45, idx + 45);
  return list;
}

function drawResults(sheet: Excel.Worksheet, list: Item[], fmt: string): void {
  sheet.getRange("A2:I" + (list.length + 1)).values = list.map((x) => [x.key, x.monday, x.tuesday, x.wednesday, x.thursday, x.friday, x.saturday, "0", x.total()]);
  sheet.getRange("B4").numberFormat = [[fmt]];
  sheet.getRange("B2:I" + (list.length + 1)).numberFormat = list.map((x) => [fmt, fmt, fmt, fmt, fmt, fmt, fmt, fmt]);
}

function loadDataForDay(range: Excel.Range, list: Item[], day: string, idxIngredient: number, idxTotal: number): void {
  for (var idx = 0; idx < limit; idx++) {
    let key = range.values[idx][idxIngredient];
    const total = range.values[idx][idxTotal];
    if (key && key !== '' && key.toLowerCase() !== "ingrediente" && key.toLowerCase() !== "pedido"
    ) {
      if (total && total !== '') {
        key = key.toLowerCase();
        var item = list.find((x) => x.key == key);
        if (item) {
          item[day] += total;
        } else {
          const newItem = new Item();
          newItem.key = key;
          newItem.monday = 0;
          newItem.tuesday = 0;
          newItem.wednesday = 0;
          newItem.thursday = 0;
          newItem.friday = 0;
          newItem.saturday = 0;
          newItem.sunday = 0;
          newItem[day] = total;
          list.push(newItem);
        }
      }
    }
  }
}

function initSheet(context: Excel.RequestContext, name: string): Excel.Worksheet {
  context.workbook.worksheets.getItemOrNullObject(name).delete();
  const sheet = context.workbook.worksheets.add(name);

  sheet.getRange("A1:I1").values = [
    ["Ingrediente", "Lunes", "Martes", "Miercoles", "Jueves", "Viernes", "Sabado", "Domingo", "Total"]
  ];
  sheet.getRange("A1:I1").format.font.italic = true;
  sheet.getRange("A1:I1").format.font.color = "DarkMagenta";
  sheet.getRange("A1:I1").format.columnWidth = 90;
  sheet.showGridlines = false;

  return sheet;
}

function initReferenceSheet(context: Excel.RequestContext): Excel.Range {
  var hpWorkSheet = context.workbook.worksheets.getItem("HP");
  var range = hpWorkSheet.getRange("B1:BA" + limit);
  range.load("values");
  range.load("numberFormat");
  return range;
}

class Item {
  key: string;
  monday?: number;
  tuesday?: number;
  wednesday?: number;
  thursday?: number;
  friday?: number;
  saturday?: number;
  sunday?: number;
  total(): number {
    return this.monday + this.tuesday + this.wednesday + this.thursday + this.friday + this.saturday + this.sunday
  };
}
